<template>
  <!-- 布局页面 用来对整个项目进行页面布局 #009687 #026460-->
  <!--<el-scrollbar style="height: 100%" class="customer-scrollbar" ref="scrollbar">-->
    <div class="page-layout">
      <!--导航栏-->
      <div class="header">
        <div class="website-name">Guotong Ritsu Cai Portfolio</div>
        <div class="nav">
          <ul class="list">
            <li class="list-item" @click="goTo('aboutMe')">About me</li>
            <li class="list-item" @click="goTo('home')">Games</li>
            <li class="list-item" @click="goTo('artwork')">Artwork</li>
          </ul>
        </div>
        <!--<ul ref="navul">-->
          <!--<li v-for="(item, index) in menu" :key="index" ref="li" @click="liClick(index, item.index)">-->
            <!--<router-link :to="item.index"><img :src="item.imgSource"/>{{item.name}}</router-link>-->
          <!--</li>-->
        <!--</ul>-->
        <!--<div class="clearfix"></div>-->
      </div>
      <!--页面主体内容-->
      <div class="page-layout-main">
        <!--和侧边栏菜单联动的Tab-->
        <transition name="fade" mode="out-in">
          <router-view v-if="showView"></router-view>
        </transition>
      </div>
    </div>
  <!--</el-scrollbar>-->
</template>

<script>
export default {
  mounted() {
    // window.addEventListener('beforeunload', e => this.beforeUnloadHandler(e))
    // this.$refs['li'][this.activeMenu - 1].classList.add('active')
    // this.getAllNewsType()
  },
  beforeDestroy() {
    // window.removeEventListener('beforeunload', e => {})
  },
  data() {
    return {
      isFullScreen: false,
      // 视图显示
      showView: true
    }
  },
  methods: {
    goTo(link) {
      if (link === 'artwork') {
        let path = 'http://ritsu37.mysxl.cn'
        window.location.href = path
      } else {
        this.$router.push('/' + link)
      }
    }
  },
  watch: {
    activeMenu: {
      handler: function(val) {
        for (let i = 0; i < this.menu.length; i++) {
          this.$refs['li'][i].classList.remove('active')
        }
        this.$refs['li'][val - 1].classList.add('active')
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  // 菜单收缩展开过渡动画
  .fade-enter-active, .fade-leave-active {
    transition: all .3s ease;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  .page-layout {
    height: 100%;
    min-width: 1500px;
    background-color: #373737;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: hidden;
    .header {
      /*width: 1200px;*/
      width: 100%;
      height: 75px;
      margin: 0 auto;
      background: #8AE5FD;
      display: flex;
      align-items: center;
      position: relative;
      .website-name {
        color: #FCFEFF;
        margin-left: 20px;
        font-size: 30px;
      }
      .nav {
        position: absolute;
        right: 0;
        .list {
          display: flex;
        }
        .list-item {
          height: 75px;
          width: 150px;
          line-height: 75px;
          color: #FCFEFF;
          font-size: 30px;
          text-align: center;
          cursor: pointer;
        }
      }
    }
    .page-layout-main {
      width: 100%;
      height: calc(100% - 75px);
      box-sizing: border-box;
      overflow-y: auto;
      overflow-x: hidden;
      background-color: #FFEBB0;
      &::-webkit-scrollbar {
        width: 8px;
        height: 260px;
        background-color: transparent;
      }
      &::-webkit-scrollbar-track {
        background-color: #ffffff;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #ededed;
        border-radius: 6px;
        border: 1px solid #E5EBF2;
      }
    }
  }
</style>
